<template>
  <v-container>
    <v-row justify="center">
      <v-card width="100%">
        <v-app-bar text dense color="transparent" elevation="0">
          <v-toolbar-title>
            <h4>Upload Source Cobol</h4>
          </v-toolbar-title>
          <v-spacer />
          <v-btn color="primary" @click="goToFunctionList()">List Function</v-btn>
        </v-app-bar>
        <v-divider></v-divider>
        <v-form enctype="multipart/form-data" novalidate v-if="isInitial || isSaving">
          <div class="dropbox">
            <input
              type="file"
              multiple
              :name="uploadFieldName"
              :disabled="isSaving"
              @change="filesChange($event.target.name, $event.target.files); fileCount = $event.target.files.length"
              accept="*"
              class="input-file"
            />
            <p v-if="isInitial">
              Drag your file(s) here to begin
              <br />or click to browse
            </p>
            <p v-if="isSaving">Uploading {{ fileCount }} files...</p>
          </div>
        </v-form>
        <v-alert v-model="error" dense outlined type="error" dismissible>{{errMsg}}</v-alert>
        <v-app-bar text dense color="transparent" elevation="0">
          <v-spacer />
          <v-btn class="mx-2" color="primary" @click="deleteSource()">Delete</v-btn>
          <v-btn color="primary" @click="convertSource()" :loading="loading">Convert</v-btn>
        </v-app-bar>
        <v-divider></v-divider>
        <v-data-table
          v-model="selected"
          :headers="headers"
          :items="cobolList"
          item-key="name"
          show-select
          class="elevation-1"
        >
          <template v-slot:item.action="{ item }">
            <v-icon @click="deleteItem(item)">delete</v-icon>
          </template>
        </v-data-table>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import { storage } from "../../firebaseConfig.js";
import axios from "axios";

const STATUS_INITIAL = 0,
  STATUS_SAVING = 1;
export default {
  name: "Upload",
  data() {
    return {
      imageData: null,
      picture: null,
      uploadValue: 0,
      selected: [],
      headers: [
        {
          text: "File name",
          align: "left",
          sortable: true,
          value: "name"
        },
        { text: "Last Modified", value: "lastModified" },
        { text: "File size", value: "size" },
        { text: "Action", value: "action", sortable: false }
      ],
      cobolList: [],
      uploadedFiles: [],
      uploadError: null,
      currentStatus: null,
      uploadFieldName: "photos",
      loading: false,
      error: false,
      errMsg: ""
    };
  },
  created() {
    this.reset();
    this.getCobolSource();
  },
  computed: {
    isInitial() {
      return this.currentStatus === STATUS_INITIAL;
    },
    isSaving() {
      return this.currentStatus === STATUS_SAVING;
    }
  },
  methods: {
    previewImage(event) {
      this.uploadValue = 0;
      this.picture = null;
      this.imageData = event.target.files[0];
    },
    getCobolSource() {
      this.cobolList = [];
      storage
        .ref(
          `akacon/projects/${this.$store.state.currentUser.uid}/${this.$route.params.projectId}/sourceA/`
        )
        .listAll()
        .then(
          function(res) {
            res.items.forEach(itemRef => {
              // Get metadata properties
              itemRef
                .getMetadata()
                .then(
                  function(metadata) {
                    this.cobolList.push({
                      fullPath: metadata.fullPath,
                      name: metadata.name,
                      type: metadata.contentType,
                      size: metadata.size
                    });
                  }.bind(this)
                )
                .catch(function(error) {
                  console.log(error.response);
                });
            });
          }.bind(this)
        )
        .catch(function(error) {
          console.log(error.response);
        });
    },
    deleteItem(item) {
      storage
        .ref(item.fullPath)
        .delete()
        .then(function() {
          this.getCobolSource();
        })
        .catch(function(error) {
          console.log(error.response);
        });
    },
    reset() {
      // reset form to initial state
      this.currentStatus = STATUS_INITIAL;
      this.uploadedFiles = [];
      this.uploadError = null;
    },
    async filesChange(fieldName, fileList) {
      if (!fileList.length) return;

      this.currentStatus = STATUS_SAVING;
      await Array.from(Array(fileList.length).keys()).map(async i => {
        const storageRef = storage
          .ref(
            `akacon/projects/${this.$store.state.currentUser.uid}/${this.$route.params.projectId}/sourceA/${fileList[i].name}`
          )
          .put(fileList[i]);
        storageRef.on(
          `state_changed`,
          snapshot => {
            this.uploadValue =
              (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          },
          error => {
            console.log(error.message);
          },
          () => {
            this.uploadValue = 100;
            this.getCobolSource();
          }
        );
      });
      this.currentStatus = STATUS_INITIAL;
    },
    goToFunctionList() {
      this.$router.push(`/${this.$route.params.projectId}/function/list`);
    },
    deleteSource() {},
    async convertSource() {
      const functionList = this.selected.map(item => item.name);
      this.loading = true;
      const token = await this.$store.state.currentUser.getIdToken();
      axios
        .post(
          "https://us-central1-akacon-f132f.cloudfunctions.net/cobol2java/convert",
          {
            projectId: this.$route.params.projectId,
            functionList: functionList
          },
          {
            headers: { Authorization: "Bearer " + token }
          }
        )
        .then(() => {
          this.$router.replace(
            `/${this.$route.params.projectId}/function/list`
          );
        })
        .catch(err => {
          console.log(err.response);
          this.error = true;
          this.errMsg = err.response.data
          this.loading = false;
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.dropbox {
  outline: 2px dashed grey; /* the dash box */
  outline-offset: -10px;
  background: lightcyan;
  color: dimgray;
  padding: 10px 10px;
  min-height: 200px; /* minimum height */
  position: relative;
  cursor: pointer;
}

.input-file {
  opacity: 0; /* invisible but it's there! */
  width: 100%;
  height: 200px;
  position: absolute;
  cursor: pointer;
}

.dropbox:hover {
  background: lightblue; /* when mouse over to the drop zone, change color */
}

.dropbox p {
  font-size: 1.2em;
  text-align: center;
  padding: 50px 0;
}
</style>