<template>
  <div id="template-management" class="acoda-container">
    <v-card height="100%">
      <v-container>
        <v-row dense>
          <v-col v-for="(item, i) in items.lesson" :key="i" cols="12">
            <v-card
              @click="
                openLesson(item);
                return;
              "
              style="cursor: pointer"
            >
              <div class="d-flex flex-no-wrap justify-space-between">
                <div>
                  <v-card-title
                    class="text-h5"
                    v-text="item.name"
                  ></v-card-title>

                  <v-card-subtitle v-text="item.name"></v-card-subtitle>
                </div>
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    items: [],
  }),

  created() {
    this.getDetailCourse();
  },

  methods: {
    async getDetailCourse() {
      console.log(this.$route.params.path);
      axios
        .get(
          "https://raw.githubusercontent.com/nguyenthedan/readwithdan/main/" +
            this.$route.params.path
        )
        .then((res) => {
          console.log(res.data);
          this.items = res.data;
        });
    },
    openLesson(item) {
      console.log("open lesson");
      this.$router.push({
        name: "Course Book",
        params: {
          path: item.path,
        },
      });
    },
  },
};
</script>