<template>
<v-container fill-height>
    <v-row justify="center">
        <v-col md="4">
            <v-card>
                <v-card-title>
                </v-card-title>
                <v-img :src="require('@/assets/logo.svg')" class="my-3" contain height="200" />
                <v-alert v-if="error" type="error">
                    {{error}}
                </v-alert>
                <v-card-text>
                    <p class="mb-3">Name</p>

                    <v-text-field
                    placeholder="name" 
                    solo 
                    value 
                    required 
                    autofocus 
                    v-model="form.name" />
                    <p class="mb-3">Email</p>

                    <v-text-field
                    placeholder="email" 
                    solo 
                    value 
                    required 
                    autofocus 
                    v-model="form.email" />

                    <p class="mb-3">Password</p>

                    <v-text-field type="password" 
                    placeholder="password" 
                    solo 
                    value 
                    required 
                    autofocus 
                    v-model="form.password"
                    />
                </v-card-text>
                <v-card-actions>
                    <v-row justify="center" align="center">
                        <v-col cols="6">
                            <v-btn color="primary" block @click="handleSignUp()">Register</v-btn>
                        </v-col>
                        <v-col cols="6" class="text-right">
                            <router-link to="/foo">Forgot password?</router-link>
                        </v-col>
                    </v-row>
                </v-card-actions>
                <v-card-actions class="justify-center" align="center">
                    <p>
                        Have an account?
                        <router-link to="/login">Login.</router-link>
                    </p>
                </v-card-actions>
            </v-card>
        </v-col>
    </v-row>
</v-container>
</template>


<script>
import { auth, db } from "../firebaseConfig.js";

export default {
  data() {
    return {
      form: {
        name: "",
        email: "",
        password: ""
      },
      error: null
    };
  },
  methods: {
    handleSignUp() {
      auth
        .createUserWithEmailAndPassword(this.form.email, this.form.password)
        .then(data => {
          data.user.sendEmailVerification();
          // create user obj
          db
            .ref("users/" + data.user.uid)
            .set({
              name: this.form.name,
              email: this.form.email
            })
            .then(function() {
              this.error = "Register success, please confirm email"
            }.bind(this))
            .catch(function(err) {
              this.errorMsg = err.message;
            }.bind(this));
        })
        .catch(err => {
          this.error = err.message;
        });
    }
  }
};
</script>

<style lang="css" scoped>
.v-card__actions {
  padding: 16px !important;
}
</style>