<template>
  <div id="template-management" class="acoda-container">
    <v-card>
      <v-card-title>
        Course
        <v-spacer></v-spacer>
        <v-btn class="mr-2" text icon>
          <v-icon @click="reloadTemplateList">mdi-reload</v-icon>
        </v-btn>
      </v-card-title>
      <v-data-iterator
        :items="courseList"
        :items-per-page.sync="ipp"
        :page.sync="page"
        hide-default-footer
      >
        <template v-slot:default="props">
          <v-row style="margin: 0 10px">
            <v-col
              v-for="item in props.items"
              :key="item.templatePath"
              :cols="12 / itemsPerRow"
              class="py-2"
            >
              <v-card class="card">
                <v-img
                  height="250"
                  :src="item.img"
                  @click="open(item)"
                  style="cursor: pointer"
                ></v-img>
                <v-card-title>
                  {{ item.title }}
                </v-card-title>
                <v-card-text>
                  <p class="text--primary">
                    {{ item.description }}
                  </p>
                  <v-chip-group>
                    <v-chip v-for="element in item.tags" :key="element">
                      {{ element }}
                    </v-chip>
                  </v-chip-group>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
        </template>
        <template v-slot:footer>
          <v-row
            class="no-gutters mt-auto py-2 shrink"
            align="center"
            justify="center"
          >
            <v-spacer></v-spacer>
            <span class="mr-4 grey--text">
              Page {{ page }} of
              <span
                v-text="numberOfPages"
                class="font-weight-bold primary--text"
              ></span>
            </span>
            <v-btn
              small
              dark
              rounded
              color="blue darken-3"
              class="mr-1"
              @click="formerPage"
            >
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
            <v-btn
              small
              dark
              rounded
              color="blue darken-3"
              class="ml-1"
              @click="nextPage"
            >
              <v-icon>mdi-chevron-right</v-icon>
            </v-btn>
          </v-row>
        </template>
      </v-data-iterator>
    </v-card>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";

export default {
  data() {
    return {
      courseList: [],
      rpp: 4,
      page: 1,
    };
  },
  computed: {
    ...mapState({
      rootFolder: (state) => state.acoda.rootFolder,
    }),

    numberOfPages() {
      return Math.ceil(this.courseList.length / this.ipp);
    },
    rowsPerPage() {
      return this.rpp;
    },
    itemsPerRow() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 1;
        case "sm":
          return 2;
        case "md":
          return 3;
        case "lg":
        case "xl":
          return 4;
        default:
          return 3;
      }
    },
    ipp() {
      return Math.ceil(this.rowsPerPage * this.itemsPerRow);
    },
  },
  watch: {},
  methods: {
    async getAllCourse(filePath) {
      if (filePath == null) {
        filePath = "course/getAllCourse.json";
      }
      axios
        .get(
          "https://raw.githubusercontent.com/nguyenthedan/readwithdan/main/getAllCourse.json"
        )
        .then((res) => {
          console.log(res.data);
          this.courseList = res.data;
        });
      // this.courseList = [
      //   {
      //     id: 1,
      //     title: "AWS",
      //     description: "AWS Basic",
      //     img: "https://img-c.udemycdn.com/course/240x135/1035000_c1aa_6.jpg",
      //     path: "course/courses/aws/index.json",
      //     tags: ["aws"],
      //   },
      //   {
      //     id: 2,
      //     title: "Java",
      //     description: "Java Basic",
      //     img: "https://img-c.udemycdn.com/course/240x135/1035000_c1aa_6.jpg",
      //     path: "course/courses/java/index.json",
      //     tags: ["java"],
      //   },
      // ];
    },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    open(item) {
      this.$router.push({
        name: "Course Detail",
        params: {
          path: item.path,
          isBuildIn: item.isBuildIn,
        },
      });
    },

    async reloadTemplateList() {
      this.getAllCourse();
    },
  },
  created() {
    this.getAllCourse();
  },
};
</script>

<style lang="css" scoped>
</style>