<template>
  <v-container>
    <v-row justify="center">
      <v-col md="4">
        <v-card>
          <v-card-title>New Project</v-card-title>
          <v-alert v-if="error" type="error">{{error}}</v-alert>
          <v-card-text>
            <p class="mb-3">Project Name</p>

            <v-text-field placeholder="name" solo value required autofocus v-model="form.name" />

            <v-select :items="projectTypes" v-model="form.type" filled solo label="Select project type"></v-select>
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" @click="handleCreateProject()">Create</v-btn>
            <v-btn @click="handleCancel()">Cancel</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from "../firebaseConfig.js";

export default {
  data() {
    return {
      form: {
        name: "",
        type: "cobol2java"
      },
      error: null,
      projectTypes: ["cobol2java", "vb2vbnet"]
    };
  },
  methods: {
    handleCreateProject() {
      db.ref("/projects/" + this.$store.state.currentUser.uid)
        .push({
          name: this.form.name,
          type: this.form.type,
          createdAt: new Date()
        })
        .catch(error => {
          console.log("Error adding document: ", error);
        });
      this.$router.back();
    },
    handleCancel() {
      this.$router.back();
    }
  }
};
</script>

<style lang="css" scoped>
.v-card__actions {
  padding: 16px !important;
}
</style>