<template>
  <v-container fluid grid-list-xl>
    <v-layout row wrap>
      <!-- Widgets-->
      <v-flex d-flex lg12 sm12 xs12>
        <v-tabs background-color="primary" dark>
          <v-tab v-for="item in items" :key="item.id" @click="switchTab(item.id)">{{ item.tab }}</v-tab>
        </v-tabs>
      </v-flex>

      <v-flex xs4>
        <v-select :items="patternItems" label="Select pattern" @change="filterPattern"></v-select>
      </v-flex>

      <v-flex xs4>
        <v-select :items="resultItems" label="Select result" @change="filterResult"></v-select>
      </v-flex>
      <v-flex xs4>
        <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="dataTableDisplay"
          :search="search"
          :options="{
            itemsPerPage: 100
          }"
        >
          <template v-slot:top>
            <v-dialog v-model="dialog" max-width="500px">
              <v-card>
                <v-card-title>
                  <span class="headline">Edit Item</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-textarea v-model="editedItem.cobol" label="Cobol"></v-textarea>
                      </v-col>
                      <v-col cols="12">
                        <v-select
                          :items="patternItems"
                          label="Select pattern"
                          v-model="editedItem.pattern"
                        ></v-select>
                      </v-col>
                      <v-col cols="12">
                        <v-textarea v-model="editedItem.java" label="Java"></v-textarea>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </template>
          <template v-slot:item.action="{ item }">
            <v-icon small class="mr-2" @click="addItem(item)">add</v-icon>
            <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
            <v-icon small class="mr-2" @click="manualReviewItem(item)">check</v-icon>
            <v-icon small @click="deleteItem(item)">delete</v-icon>
          </template>
          <template v-slot:item.cobol="{ item }">
            <v-tooltip bottom v-if="!item.result">
              <template v-slot:activator="{ on }">
                <span v-on="on">{{ item.cobol }}</span>
              </template>
              <span>{{ item.error }}</span>
            </v-tooltip>
            <span v-if="item.result">{{ item.cobol }}</span>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import { storage } from "../../firebaseConfig.js";

export default {
  name: "ProjectDashboard",
  components: {},
  data() {
    return {
      patternItems: [],
      resultItems: ["All", "true", "false"],
      items: [
        { tab: "Logic Mapping", id: "1" },
        { tab: "Data Mapping", id: "2" }
      ],
      search: "",
      filterPatternValue: "",
      filterResultValue: "",
      loaded: false,

      // var for table
      dialog: false,
      headers: [
        {
          text: "ID",
          align: "left",
          sortable: false,
          filterable: false,
          value: "id",
          width: "5%"
        },
        { text: "Cobol", sortable: false, value: "cobol", width: "35%" },
        { text: "Pattern", sortable: false, value: "pattern", width: "15%" },
        { text: "Java", sortable: false, value: "java", width: "35%" },
        {
          text: "Action",
          sortable: false,
          filterable: false,
          value: "action",
          width: "10%"
        }
      ],
      dataTableMapping: [],
      dataTableDisplay: [],
      editedItem: {
        cobol: "",
        pattern: "",
        java: ""
      },
      tab: 1 // logic
    };
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  mounted() {},
  created() {
    this.getMappingSource("logic.json");
  },
  computed: {},
  methods: {
    getColor(status) {
      if (status == 1) return "red";
      else return "green";
    },
    getMappingSource(fileMapping) {
      this.loaded = false;
      storage
        .ref(
          `akacon/projects/${this.$store.state.currentUser.uid}/${this.$route.params.projectId}/${this.$route.params.functionId}/mapping/${fileMapping}`
        )
        .getDownloadURL()
        .then(
          function(url) {
            axios
              .get(url)
              .then(
                function(res) {
                  this.dataTableMapping = res.data;
                  this.dataTableDisplay = res.data;
                  let allPattern = res.data.map(item => {
                    if(typeof(item.pattern) == 'undefined') {
                      return ""
                    }
                    return item.pattern;
                  });
                  allPattern.push("All");
                  this.patternItems = [...new Set(allPattern.sort())];
                  this.loaded = true;
                }.bind(this)
              )
              .catch(err => {
                console.log(err.response);
                // this.loaded = true;
              });
          }.bind(this)
        );
    },

    switchTab(tab) {
      if (tab == "1") {
        this.tab = 1;
        this.getMappingSource("logic.json");
      } else if (tab == "2") {
        this.getMappingSource("data.json");
        this.tab = 2;
      }
    },

    filterPattern(value) {
      if (value == "All") {
        value = "";
      }
      this.filterPatternValue = value;
      this.filterTable();
    },

    filterResult(value) {
      if (value == "All") {
        value = "";
      }
      this.filterResultValue = value;
      this.filterTable();
    },

    filterTable() {
      if (this.filterResultValue == "" && this.filterPatternValue == "") {
        this.dataTableDisplay = this.dataTableMapping;
      } else if (this.filterResultValue == "") {
        this.dataTableDisplay = this.dataTableMapping.filter(
          item => item.pattern == this.filterPatternValue
        );
      } else if (this.filterPatternValue == "") {
        this.dataTableDisplay = this.dataTableMapping.filter(
          item => item.result == JSON.parse(this.filterResultValue)
        );
      } else {
        this.dataTableDisplay = this.dataTableMapping.filter(
          item =>
            item.pattern == this.filterPatternValue &&
            item.result == JSON.parse(this.filterResultValue)
        );
      }
    },
    async addItem(item) {
      let type = "logic";
      if (this.tab == "1") {
        type = "logic";
      } else if (this.tab == "2") {
        type = "data";
      }
      const token = await this.$store.state.currentUser.getIdToken();
      axios
        .post(
          "https://us-central1-akacon-f132f.cloudfunctions.net/cobol2java/mapping/add",
          {
            projectId: this.$route.params.projectId,
            functionId: this.$route.params.functionId,
            id: item.id,
            type: type
          },
          {
            headers: { Authorization: "Bearer " + token }
          }
        )
        .then(
          function() {
            let addItem = Object.assign({}, item);
            addItem.error = "";
            addItem.result = false;
            let addItemIndex = parseInt(addItem.id);
            addItem.id = (addItemIndex + 1).toString();
            this.dataTableMapping.splice(item.id, 0, addItem);
            this.dataTableMapping.forEach((item, index) => {
              if (index > addItemIndex) {
                item.id = index + 1;
              }
            });
            this.filterTable();
          }.bind(this)
        )
        .catch(
          function(err) {
            console.log(err.response);
          }.bind(this)
        );
    },

    // table action
    editItem(item) {
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    async deleteItem(item) {
      let type = "logic";
      if (this.tab == "1") {
        type = "logic";
      } else if (this.tab == "2") {
        type = "data";
      }
      const token = await this.$store.state.currentUser.getIdToken();
      axios
        .post(
          "https://us-central1-akacon-f132f.cloudfunctions.net/cobol2java/mapping/delete",
          {
            projectId: this.$route.params.projectId,
            functionId: this.$route.params.functionId,
            id: item.id,
            type: type
          },
          {
            headers: { Authorization: "Bearer " + token }
          }
        )
        .then(
          function() {
            let itemIndex = parseInt(item.id) - 1;
            this.dataTableMapping.splice(itemIndex, 1);
            this.dataTableMapping.forEach((item, index) => {
              if (index >= itemIndex) {
                item.id = item.id - 1;
              }
            });
            this.filterTable();
          }.bind(this)
        )
        .catch(
          function(err) {
            console.log(err.response);
          }.bind(this)
        );
    },

    async manualReviewItem(item) {
      let type = "logic";
      if (this.tab == "1") {
        type = "logic";
      } else if (this.tab == "2") {
        type = "data";
      }
      const token = await this.$store.state.currentUser.getIdToken();
      axios
        .post(
          "https://us-central1-akacon-f132f.cloudfunctions.net/cobol2java/mapping/ok",
          {
            projectId: this.$route.params.projectId,
            functionId: this.$route.params.functionId,
            id: item.id,
            type: type
          },
          {
            headers: { Authorization: "Bearer " + token }
          }
        )
        .then(
          function() {
            let reviewItemIndex = parseInt(item.id) - 1;
            this.dataTableMapping[reviewItemIndex].result = true;
            this.filterTable();
          }.bind(this)
        )
        .catch(
          function(err) {
            console.log(err.response);
          }.bind(this)
        );
    },

    // dialog action
    close() {
      this.dialog = false;
    },

    async save() {
      let type = "logic";
      if (this.tab == "1") {
        type = "logic";
      } else if (this.tab == "2") {
        type = "data";
      }
      const token = await this.$store.state.currentUser.getIdToken();
      axios
        .post(
          "https://us-central1-akacon-f132f.cloudfunctions.net/cobol2java/mapping/edit",
          {
            projectId: this.$route.params.projectId,
            functionId: this.$route.params.functionId,
            id: this.editedItem.id,
            type: type,
            cobol: this.editedItem.cobol,
            pattern: this.editedItem.pattern,
            java: this.editedItem.java
          },
          {
            headers: { Authorization: "Bearer " + token }
          }
        )
        .then(
          function() {
            let editedItemIndex = parseInt(this.editedItem.id) - 1;
            this.dataTableMapping[
              editedItemIndex
            ].cobol = this.editedItem.cobol;
            this.dataTableMapping[
              editedItemIndex
            ].pattern = this.editedItem.pattern;
            this.dataTableMapping[editedItemIndex].java = this.editedItem.java;
            this.filterTable();
          }.bind(this)
        )
        .catch(
          function(err) {
            console.log(err.response);
          }.bind(this)
        );
      this.close();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
table tbody tr td {
  white-space: pre-wrap;
  font: 11px ＭＳ ゴシック, sans-serif;
}
</style>