<template>
  <v-container fluid grid-list-xl>
    <div v-if="status">
      <v-flex d-flex lg12 sm12 xs12>
        <v-toolbar-title>
          <h4>Convert Log</h4>
        </v-toolbar-title>
      </v-flex>
      <p style="white-space: pre-wrap;">{{convertLog}}</p>
    </div>
    <v-layout row wrap v-if="!status">
      <v-flex d-flex lg12 sm12 xs12>
        <v-toolbar-title>
          <h4>Function Dashboard</h4>
        </v-toolbar-title>
        <v-spacer />

        <v-btn class="mx-2" color="primary" @click="downloadSource()">Export Source</v-btn>
        <v-btn color="primary" @click="goToMapping()">Go To Mapping</v-btn>
      </v-flex>
      <!-- Widgets-->
      <v-flex d-flex lg3 sm6 xs12>
        <widget
          icon="domain"
          :title="dashboard.okPattern"
          subTitle="13% higher yesterday"
          supTitle="Pattern OK"
          color="#00b297"
        />
      </v-flex>
      <v-flex d-flex lg3 sm6 xs12>
        <widget
          icon="money_off"
          :title="dashboard.ngPattern"
          subTitle="8% lower yesterday"
          supTitle="Pattern NG"
          color="#dc3545"
        />
      </v-flex>
      <v-flex d-flex lg3 sm6 xs12>
        <widget
          icon="computer"
          :title="dashboard.totalPattern"
          subTitle="3% higher yesterday"
          supTitle="Total pattern"
          color="#0866C6"
        />
      </v-flex>
      <v-flex d-flex lg3 sm6 xs12>
        <widget
          icon="watch_later"
          :title="dashboard.numPattern"
          subTitle="1% higher yesterday"
          supTitle="Pattern type"
          color="#1D2939"
        />
      </v-flex>
      <!-- Widgets Ends -->

      <!-- Statistics -->
      <v-flex d-flex lg6 sm6 xs12>
        <v-card width="100%">
          <v-card-title>
            Pattern
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table :headers="headers" :items="dataTablePattern" :search="search"></v-data-table>
        </v-card>
      </v-flex>
      <!-- Statistics Ends -->
      <v-flex d-flex lg3 sm3 xs6>
        <v-card width="100%">
          <v-card-title>Pattern NG</v-card-title>
          <pie-chart v-if="loaded" :data="chartPatternNG" :options="chartOptions"></pie-chart>
        </v-card>
      </v-flex>
      <v-flex d-flex lg3 sm3 xs6>
        <v-card width="100%">
          <v-card-title>Pattern NA</v-card-title>
          <pie-chart v-if="loaded" :data="chartPatternNA" :options="chartOptions"></pie-chart>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import { storage } from "../../firebaseConfig.js";
import PieChart from "./PieChart.js";
import Util from "@/utils";

export default {
  name: "ProjectDashboard",
  components: {
    PieChart
  },
  data() {
    return {
      status: true,
      convertLog: "",
      search: "",
      loaded: false,
      dashboard: {},
      headers: [
        {
          text: "Project name",
          align: "left",
          value: "name"
        },
        { text: "Pattern OK", value: "countCorrect", filterable: false },
        { text: "Pattern NG", value: "countNotCorrect", filterable: false },
        { text: "Pattern N/A", value: "countTotal", filterable: false },
        { text: "Total", value: "countTotal", filterable: false }
      ],
      dataTablePattern: [],
      chartOptions: {
        hoverBorderWidth: 50,
        legend: {
          display: false,
          labels: {
            display: false
          }
        },
        hoverBackgroundColor: "black"
      },
      chartPatternNG: {
        labels: [],
        datasets: [
          {
            label: "Pattern NG",
            backgroundColor: [],
            data: []
          }
        ]
      },
      chartPatternNA: {
        labels: [],
        datasets: [
          {
            label: "Pattern NA",
            backgroundColor: [],
            data: []
          }
        ]
      }
    };
  },
  mounted() {},
  created() {
    this.getCobolSource();
  },
  computed: {},
  methods: {
    getCobolSource() {
      this.loaded = false;
      storage
        .ref(
          `akacon/projects/${this.$store.state.currentUser.uid}/${this.$route.params.projectId}/${this.$route.params.functionId}/mapping/dashboard.json`
        )
        .getDownloadURL()
        .then(
          function(url) {
            axios
              .get(url)
              .then(
                function(res) {
                  this.status = false
                  this.dashboard.okPattern = res.data.okPattern;
                  this.dashboard.ngPattern = res.data.ngPattern;
                  this.dashboard.totalPattern = res.data.totalPattern;
                  this.dashboard.numPattern = res.data.numPattern;

                  this.dataTablePattern = res.data.patternList;
                  res.data.patternList.forEach(item => {
                    this.chartPatternNG.labels.push(item.name);
                    this.chartPatternNG.datasets[0].data.push(
                      item.countNotCorrect
                    );
                    this.chartPatternNG.datasets[0].backgroundColor.push(
                      Util.getRandomColor()
                    );
                    this.chartPatternNA.labels.push(item.name);
                    this.chartPatternNA.datasets[0].data.push(
                      item.countTotal - item.countCorrect - item.countNotCorrect
                    );
                    this.chartPatternNA.datasets[0].backgroundColor.push(
                      Util.getRandomColor()
                    );
                  });
                  this.loaded = true;
                }.bind(this)
              )
              .catch(
                function(err) {
                  console.log(err.response);
                  this.loaded = true;
                }.bind(this)
              );
          }.bind(this)
        )
        .catch(
          function(err) {
            console.log(err.response);
            console.log("start get sourceA log");
            storage
              .ref(
                `akacon/projects/${this.$store.state.currentUser.uid}/${this.$route.params.projectId}/${this.$route.params.functionId}/log/akacon.log`
              )
              .getDownloadURL()
              .then(
                function(url) {
                  axios
                    .get(url)
                    .then(
                      function(res) {
                        this.status = true
                        this.convertLog = res.data;
                        this.loaded = true;
                      }.bind(this)
                    )
                    .catch(
                      function(err) {
                        console.log(err.response);
                        this.loaded = true;
                      }.bind(this)
                    );
                }.bind(this)
              );
          }.bind(this)
        );
    },
    downloadSource() {
      this.$router.push("/newproject");
    },
    goToMapping() {
      this.$router.push(
        `/${this.$route.params.projectId}/${this.$route.params.functionId}/mapping`
      );
    }
  }
};
</script>

