<template>
  <v-app-bar color="primary" dark app>
    <v-app-bar-nav-icon @click="handleDrawerToggle" />
    <v-spacer />
    <v-toolbar-items>
      <v-menu offset-y origin="center center" class="elelvation-1" transition="scale-transition">
        <template v-slot:activator="{ on }">
          <v-btn icon text slot="activator" v-on="on">
            <v-badge color="red" overlap>
              <span slot="badge">1</span>
              <v-icon medium>notifications</v-icon>
            </v-badge>
          </v-btn>
        </template>
        <notification-list></notification-list>
      </v-menu>
      <v-menu offset-y origin="center center" transition="scale-transition">
        <template v-slot:activator="{ on }">
          <v-btn icon large text slot="activator" v-on="on">
            <v-avatar size="30px">
              <img :src="require('../assets/logo.svg')" :alt="username" />
            </v-avatar>
          </v-btn>
        </template>
        <v-list class="pa-0">
          <v-list-item
            v-for="(item, index) in profileMenus"
            :to="!item.href ? { name: item.name } : null"
            :href="item.href"
            @click="item.click"
            ripple="ripple"
            :disabled="item.disabled"
            :target="item.target"
            rel="noopener"
            :key="index"
          >
            <v-list-item-action v-if="item.icon">
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-toolbar-items>
    <v-toolbar dense slot="extension" color="white" light>
      <v-icon>mdi-home</v-icon>
      <v-breadcrumbs :items="breadcrumbs" class="pa-3"></v-breadcrumbs>
    </v-toolbar>
  </v-app-bar>
</template>
<script>
import { auth } from "../firebaseConfig.js";
import { mapGetters } from "vuex";
import NotificationList from "@/components/widgets/list/NotificationList";
export default {
  name: "AppToolbar",
  components: {
    NotificationList
  },
  data() {
    return {
      profileMenus: [
        {
          icon: "account_circle",
          href: "#",
          title: "Profile",
          click: this.handleProfile
        },
        {
          icon: "settings",
          href: "#",
          title: "Settings",
          click: this.handleSetting
        },
        {
          icon: "fullscreen_exit",
          href: "#",
          title: "Logout",
          click: this.handleLogut
        }
      ]
    };
  },
  computed: {
    toolbarColor() {
      return this.$vuetify.options.extra.mainNav;
    },
    breadcrumbs() {
      const items = [
        {
          text: "Home",
          to: { path: "/" }
        }
      ];
      if (["/", "/dashboard"].includes(this.$route.path) === false) {
        items.push({
          text: this.$route.meta.title,
          to: { path: this.$route.path }
        });
      }
      return items;
    },
    ...mapGetters({
      // map `this.user` to `this.$store.getters.user`
      username: "username"
    })
  },
  methods: {
    handleDrawerToggle() {
      this.$emit("side-icon-click");
    },
    handleLogut() {
      auth.signOut().then(() => {
        this.$router.replace({
          name: "Login"
        });
      });
    },
    handleSetting() {},
    handleProfile() {}
  },
  created() {}
};
</script>

<style lang="sass" scoped></style>
