<script>
import { mapActions } from 'vuex'

export default {
  name: 'tweet',
  data() {
    return {}
  },
  props: {
    tweetData: {
      type: Object
    }
  },
  methods: {
    ...mapActions('tweet', ['likeTweet']),
    ...mapActions('tweet', ['fetchTweets']),
    ...mapActions('tweet', ['retweet']),
    async submitLikeTweet(tweetId) {
      try {
        await this.likeTweet(tweetId)
      } catch (e) {
        console.log(e)
      } finally {
        this.fetchTweet()
      }
    },
    async submitRetweet(tweetId) {
      console.log('retweet')
      try {
        await this.retweet(tweetId)
      } catch (e) {
        console.log(e)
      }
      // finally {
      //   this.fetchTweet()
      // }
    }
  }
}
</script>

<template>
  <div class="container-tweet">
    <div id="tweet">
      <div class="tweet-content">
        <div class="user-info">
          <router-link :to="{ path: `/${tweetData.author.username}` }" tag="a">
            <p class="name">{{ tweetData.author.name }}</p>
          </router-link>
          <span>•</span>
          <p class="date">
            {{ tweetData.createdAt }}
          </p>
        </div>
        <div class="tweet-body">
          <p>
            {{ tweetData.content }}
            <!-- <span class="hashtag">#{{ tweetData.author.username }}</span> -->
          </p>
        </div>
        
      </div>
    </div>
  </div>
</template>
<style scoped lang="scss">
#tweet {
  display: flex;
  align-items: flex-start;
  padding: 1rem 16px 11px;
  // cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);

  &:hover {
    background-color: rgba(0, 0, 0, 0.03);
  }

  img {
    margin-right: 1rem;
    border-radius: 50%;
    width: 48px;
    height: 48px;
  }

  .tweet-content {
    width: 100%;

    .user-info {
      display: flex;
      align-items: flex-start;
      margin-bottom: 11px;

      a {
        cursor: pointer;
        display: flex;
        align-items: flex-start;
      }

      * {
        margin-right: 4px;
        line-height: 17.58px;
        font-size: 15px;
        color: #828282;
      }

      .name {
        font-weight: 700;
        color: #000;
      }

      span {
        color: #828282;
      }
    }

    .tweet-body {
      margin-bottom: 11px;

      p {
        font-size: 15px;
        line-height: 22px;
        color: #333333;
      }

      .hashtag {
        color: #1da1f2;
      }

      .hashtag:hover {
        text-decoration: underline;
      }
    }
  }
}

@media (max-width: 476px) {
  #tweet {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}
</style>
