<template>
  <div id="app" class="questionBox">
    <!--qusetionContainer-->
    <div class="questionContainer">
      <header>
        <h1 class="title is-6">VueQuiz</h1>
        <!--progress-->
        <div class="progressContainer">
          <progress
            class="progress is-info is-small"
            :value="(questionIndex / questions.length) * 100"
            max="100"
          >
            {{ (questionIndex / questions.length) * 100 }}%
          </progress>
          <p>{{ (questionIndex / questions.length) * 100 }}% complete</p>
        </div>
        <!--/progress-->
      </header>

      <!-- questionTitle -->
      <h1
        class="titleContainer title"
        v-html="loading ? 'Loading...' : currentQuestion.question"
      ></h1>

      <!-- quizOptions -->
      <div class="optionContainer" v-if="currentQuestion">
        <div
          class="option"
          v-for="(answer, index) in currentQuestion.answers"
          @click="selectOption(index)"
          :key="answer"
          :class="{ 'is-selected': selectedAnswer == index }"
        >
          {{ index }}. {{ answer }}
        </div>
      </div>

      <footer class="questionFooter">
        <!--pagination-->
        <nav class="pagination" role="navigation" aria-label="pagination">
          <!-- back button -->
          <a class="button" v-on:click="prev()" :disabled="questionIndex < 1">
            Back
          </a>

          <!-- next button -->
          <a class="button" v-on:click="checkAnswer()"> Check </a>
        </nav>
        <!--/pagination-->
      </footer>
      <!--/quizFooter-->
    </div>
  </div>
</template>

<script>
export default {
  name: "Quiz",
  data() {
    return {
      questions: [],
      loading: true,
      questionIndex: 0,
      selectedAnswer: -1,
    };
  },
  computed: {
    currentQuestion() {
      if (this.questions !== []) {
        return this.questions[this.questionIndex];
      }
      return null;
    },
    score() {
      if (this.questions !== []) {
        // Here, we want to collect data in an object about the users statistics - later be emitted on an event when users finishes quiz
        return {
          allQuestions: this.questions.length,
          answeredQuestions: this.questions.reduce((count, currentQuestion) => {
            if (currentQuestion.userAnswer) {
              // userAnswer is set when user has answered a question, no matter if right or wrong
              count++;
            }
            return count;
          }, 0),
          correctlyAnsweredQuestions: this.questions.reduce(
            (count, currentQuestion) => {
              if (currentQuestion.rightAnswer) {
                // rightAnswer is true, if user answered correctly
                count++;
              }
              return count;
            },
            0
          ),
        };
      } else {
        return {
          allQuestions: 0,
          answeredQuestions: 0,
          correctlyAnsweredQuestions: 0,
        };
      }
    },
    correctAnswers() {
      if (this.questions && this.questions.length > 0) {
        let streakCounter = 0;
        this.questions.forEach(function (question) {
          if (!question.rightAnswer) {
            return;
          } else if (question.rightAnswer === true) {
            streakCounter++;
          }
        });
        return streakCounter;
      } else {
        return "--";
      }
    },
    pluralizeAnswer() {
      // For grammatical correctness
      return this.correctAnswers === 1 ? "Answer" : "Answers";
    },
    quizCompleted() {
      if (this.questions.length === 0) {
        return false;
      }
      /* Check if all questions have been answered */
      let questionsAnswered = 0;
      this.questions.forEach(function (question) {
        question.rightAnswer !== null ? questionsAnswered++ : null;
      });
      return questionsAnswered === this.questions.length;
    },
  },
  watch: {
    quizCompleted(completed) {
      /*
       * Watcher on quizCompleted fires event "quiz-completed"
       * up to parent App.vue component when completed parameter
       * returned by quizCompleted computed property true
       */
      completed &&
        setTimeout(() => {
          this.$emit("quiz-completed", this.score);
        }, 3000); // wait 3 seconds until button animation is over
    },
  },
  methods: {
    async fetchQuestions() {
      this.loading = true;
      let response = await fetch(
        "https://opentdb.com/api.php?amount=5&category=9"
      );
      let jsonResponse = await response.json();
      let index = 0; // index is used to identify single answer
      let data = jsonResponse.results.map((question) => {
        // put answers on question into single array
        question.answers = [
          question.correct_answer,
          ...question.incorrect_answers,
        ];
        /* Shuffle question.answers array */
        for (let i = question.answers.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [question.answers[i], question.answers[j]] = [
            question.answers[j],
            question.answers[i],
          ];
        }
        // mention in Step 1
        question.rightAnswer = null;
        question.key = index;
        index++;
        return question;
      });
      this.questions = data;
      this.loading = false;
    },
    selectOption: function (index) {
      this.selectedAnswer = index;
    },
    checkAnswer: function () {
      let question = this.questions[this.questionIndex];
      let pollutedUserAnswer = question.answers[this.selectedAnswer];
            let userAnswer = pollutedUserAnswer.replace(/'/, "&#039;");
      this.questions[this.questionIndex].userAnswer = userAnswer;
      if (question.userAnswer) {
        if (this.questionIndex < this.questions.length - 1) {
          setTimeout(
            function () {
              this.questionIndex += 1;
            }.bind(this),
            3000
          );
        }
        if (question.userAnswer === question.correct_answer) {
          /* Set class on Button if user answered right, to celebrate right answer with animation joyfulButton */
          // event.target.classList.add("rightAnswer");
          /* Set rightAnswer on question to true, computed property can track a streak out of 10 questions */
          this.questions[this.selectedAnswer].rightAnswer = true;
        } else {
          /* Mark users answer as wrong answer */
          // event.target.classList.add("wrongAnswer");
          this.questions[this.selectedAnswer].rightAnswer = false;
          /* Show right Answer */
          let correctAnswer = this.questions[this.selectedAnswer].correct_answer;
          // let allButtons = document.querySelectorAll(`[index="${this.selectedAnswer}"]`);
          // allButtons.forEach(function (button) {
          //   if (button.innerHTML === correctAnswer) {
          //     button.classList.add("showRightAnswer");
          //   }
          // });
          console.log(correctAnswer);
        }
      }
    },
  },
  mounted() {
    this.fetchQuestions();
  },
};
</script>

