<template>
  <v-container fluid grid-list-xl>
    <v-layout row wrap>
      <!-- Widgets-->
      <v-flex d-flex lg12 sm12 xs12>
        <v-tabs background-color="primary" dark>
          <v-tab v-for="item in items" :key="item.id" :href="`#tab-${item.id}`">{{ item.tab }}</v-tab>
          <v-tab-item :value="'tab-1'">
            <v-flex d-flex lg12 sm12 xs12>
              <v-card width="100%">
                <v-card-title>Update project details</v-card-title>
                <v-card-text>
                  <p class="mb-3">Project name</p>
                  <v-text-field
                    placeholder="name"
                    solo
                    value
                    required
                    autofocus
                    v-model="form.name"
                  />
                  <p class="mb-3">Project type</p>
                  <v-select
                    :items="projectTypes"
                    v-model="form.type"
                    filled
                    solo
                    label="Select project type"
                  ></v-select>
                  <v-btn color="primary" @click="handleCreateProject()">Update</v-btn>
                </v-card-text>
              </v-card>
            </v-flex>

            <v-flex d-flex lg12 sm12 xs12>
              <v-card width="100%">
                <v-card-title>Transfer or delete project</v-card-title>
                <div class="d-flex justify-space-between">
                  <v-card-subtitle>Transfer this project to another account</v-card-subtitle>
                  <v-btn class="mr-4" @click="handleCreateProject()">Transfer project</v-btn>
                </div>
                <div class="d-flex justify-space-between">
                  <v-card-subtitle>Permanently delete this repository and all of its content</v-card-subtitle>
                  <v-btn class="mr-4" @click="handleDeleteProject()">Delete project</v-btn>
                </div>
              </v-card>
            </v-flex>
          </v-tab-item>

          <v-tab-item :value="'tab-2'">
            <v-flex d-flex lg12 sm12 xs12>
              <v-card width="100%">
                <v-card-title>Add plugin</v-card-title>
                <v-form enctype="multipart/form-data" novalidate>
                  <div class="dropbox">
                    <input type="file" multiple accept="*" class="input-file" />
                    <p>
                      Drag your file(s) here to begin
                      <br />or click to browse
                    </p>
                  </div>
                </v-form>
                <v-divider></v-divider>
                <v-data-table
                  :headers="headerTablePlugins"
                  :items="plugins"
                  item-key="name"
                  class="elevation-1"
                >
                  <template v-slot:item.action="{ item }">
                    <v-switch class="mx-2">
                      <template v-slot:label>
                        <v-icon @click="deleteItem(item)">delete</v-icon>
                      </template>
                    </v-switch>
                  </template>
                </v-data-table>
              </v-card>
            </v-flex>

            <v-flex d-flex lg12 sm12 xs12>
              <v-card width="100%">
                <v-card-title>Config plugins</v-card-title>
                <v-col cols="12" sm="4">
                  <v-select
                    :items="pluginsConfig"
                    v-model="selectPlugin"
                    filled
                    solo
                    label="Select plugin"
                    @change="loadConfig"
                  ></v-select>
                </v-col>
                <div class="d-flex">
                  <p class="ml-4">Plugin config {{selectPlugin}}</p>
                  <v-spacer></v-spacer>
                  <v-btn class="mr-4" @click="handleAddPattern()">Add Pattern</v-btn>
                  <v-btn class="mr-4" @click="handleSavePattern()">Save Config</v-btn>
                </div>
                <v-data-table
                  :headers="headers"
                  :items="configList"
                  item-key="name"
                  class="elevation-1"
                >
                  <template v-slot:item.active="{ item }">
                    <v-simple-checkbox v-model="item.active"></v-simple-checkbox>
                  </template>
                  <template v-slot:top>
                    <v-dialog v-model="dialog" max-width="500px">
                      <v-card>
                        <v-card-title>
                          <span class="headline">Edit Item</span>
                        </v-card-title>

                        <v-card-text>
                          <v-container>
                            <v-row>
                              <v-col cols="12">
                                <v-textarea v-model="editedItem.name" label="Name"></v-textarea>
                              </v-col>
                              <v-col cols="12">
                                <v-textarea
                                  v-model="editedItem.patternReplace"
                                  label="patternSearch"
                                ></v-textarea>
                              </v-col>
                              <v-col cols="12">
                                <v-textarea
                                  v-model="editedItem.patternReplace"
                                  label="patternReplace"
                                ></v-textarea>
                              </v-col>
                            </v-row>
                          </v-container>
                        </v-card-text>

                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                          <v-btn color="blue darken-1" text @click="add">Add</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                  </template>
                  <template v-slot:item.action="{ item }">
                    <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
                    <v-icon small @click="deleteItem(item)">delete</v-icon>
                  </template>
                </v-data-table>
              </v-card>
            </v-flex>
          </v-tab-item>
        </v-tabs>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import { db, storage } from "../../firebaseConfig.js";

export default {
  name: "ProjectSetting",
  components: {},
  data() {
    return {
      items: [
        { tab: "General", id: "1" },
        { tab: "Convert setting", id: "2" },
        { tab: "Users and permissions", id: "3" }
      ],
      tab: 1, // logic
      form: {
        name: "",
        type: "cobol2java"
      },
      error: null,
      projectTypes: ["cobol2java", "vb2vbnet"],
      plugins: [],
      selectPlugin: "",
      pluginsConfig: [],

      // table plugin
      headerTablePlugins: [
        {
          text: "File name",
          sortable: true,
          value: "name"
        },
        { text: "Action", value: "action", sortable: false }
      ],
      pluginList: [],

      // table config
      headers: [],
      configList: [], // data table config

      // dialog
      dialog: false,
      editedItem: {}
    };
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  mounted() {},
  created() {
    db.ref(
      "/projects/" +
        this.$store.state.currentUser.uid +
        "/" +
        this.$route.params.projectId
    )
      .once("value")
      .then(data => {
        let project = data.val();
        this.form.name = project.name;
        this.form.type = project.type;
      });
    this.getAllPlugins();
  },
  computed: {},
  methods: {
    handleDeleteProject() {},
    getAllPlugins() {
      this.plugins = [];
      this.pluginsConfig = [];
      this.getPlugins(
        `akacon/projects/${this.$store.state.currentUser.uid}/${this.$route.params.projectId}/plugins/`
      );
      this.getPlugins(`akacon/common/vb2vbnet/plugins/`);
    },
    getPlugins(ref) {
      storage
        .ref(ref)
        .listAll()
        .then(
          function(res) {
            res.items.forEach(itemRef => {
              // Get metadata properties
              itemRef
                .getMetadata()
                .then(
                  function(metadata) {
                    if (metadata.contentType == "application/json") {
                      this.pluginsConfig.push(metadata.name);
                    } else {
                      this.plugins.push({
                        fullPath: metadata.fullPath,
                        name: metadata.name,
                        type: metadata.contentType
                      });
                    }
                  }.bind(this)
                )
                .catch(function(error) {
                  console.log(error.response);
                });
            });
          }.bind(this)
        )
        .catch(function(error) {
          console.log(error.response);
        });
    },
    loadConfig(selectObj) {
      storage
        .ref(
          `akacon/projects/${this.$store.state.currentUser.uid}/${this.$route.params.projectId}/plugins/${selectObj}`
        )
        .getDownloadURL()
        .then(
          function(url) {
            axios
              .get(url)
              .then(
                function(res) {
                  this.headers = res.data.header;
                  this.headers.push({
                    text: "Action",
                    sortable: false,
                    filterable: false,
                    value: "action"
                  });
                  this.configList = res.data.data;
                }.bind(this)
              )
              .catch(err => {
                console.log(err.response);
                // this.loaded = true;
              });
          }.bind(this)
        );
    },
    handleAddPattern() {
      this.dialog = true;
    },
    handleSavePattern() {},

    // dialog action
    close() {
      this.dialog = false;
    },
    add() {
      let newItem = {};
      newItem.active = true;
      newItem.name = this.editedItem.name;
      newItem.patternSearch = this.editedItem.patternSearch;
      newItem.patternReplace = this.editedItem.patternReplace;
      this.configList.push(newItem);
      this.close();
    },
    editItem(item) {
      console.log(item);
    },
    deleteItem(item) {
      console.log(item);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.dropbox {
  outline: 2px dashed grey; /* the dash box */
  outline-offset: -10px;
  background: lightcyan;
  color: dimgray;
  padding: 10px 10px;
  min-height: 200px; /* minimum height */
  position: relative;
  cursor: pointer;
}

.input-file {
  opacity: 0; /* invisible but it's there! */
  width: 100%;
  height: 200px;
  position: absolute;
  cursor: pointer;
}

.dropbox:hover {
  background: lightblue; /* when mouse over to the drop zone, change color */
}

.dropbox p {
  font-size: 1.2em;
  text-align: center;
  padding: 50px 0;
}
</style>