import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";
import { db } from "../firebaseConfig.js";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        currentUser: null,
        userProfile: {},
        projects: [],
        menu: [
            { header: 'Apps' },
            {
                text: 'akacon',
                icon: 'dashboard',
                to: '/dashboard',
            },
        ]
    },
    actions: {
        clearData({ commit }) {
            commit('setCurrentUser', null)
            commit('setUserProfile', {})
            commit('setProjects', null)
        },
        fetchUserProfile({ commit, state }) {
            db.ref('/users/' + state.currentUser.uid).once('value').then(res => {
                commit('setUserProfile', res.val())
            }).catch(err => {
                console.log(err.response)
            })
        },
        // TODO
        // updateProfile({ commit, state }, data) {
        //     let name = data.name
        //     let title = data.title

        //     fb.usersCollection.doc(state.currentUser.uid).update({ name, title }).then(user => {
        //         // update all projects by user to reflect new name
        //         fb.projectsCollection.where('userId', '==', state.currentUser.uid).get().then(docs => {
        //             docs.forEach(doc => {
        //                 fb.projectsCollection.doc(doc.id).update({
        //                     userName: name
        //                 })
        //             })
        //         })
        //         // update all comments by user to reflect new name
        //         fb.commentsCollection.where('userId', '==', state.currentUser.uid).get().then(docs => {
        //             docs.forEach(doc => {
        //                 fb.commentsCollection.doc(doc.id).update({
        //                     userName: name
        //                 })
        //             })
        //         })
        //     }).catch(err => {
        //         console.log(err)
        //     })
        // }
    },
    getters: {
        username(state) {
            return state.currentUser.name
        }
    },
    mutations: {
        setCurrentUser(state, val) {
            state.currentUser = val
        },
        setUserProfile(state, val) {
            state.userProfile = val
        },
        setProjects(state, val) {
            if (val) {
                state.projects = val
            } else {
                state.projects = []
            }
        },
        setMenu(state, val) {
            state.menu.length = 0
            for (var item of val) {
                state.menu.push(item)
            }
        }
    },
    plugins: [createPersistedState()]
});