<template>
  <v-navigation-drawer app class="app--drawer" :mini-variant.sync="mini" v-model="drawer" :width="drawWidth"
    :dark="$vuetify.dark">
    <v-toolbar color="primary darken-1" dark>
      <img :src="require('../assets/logo.svg')" height="36" alt="acoda" />
      <v-toolbar-title class="ml-0 pl-3">
        <span class="hidden-sm-and-down">Acoda</span>
      </v-toolbar-title>
    </v-toolbar>
    <v-list expand dense v-if="items" nav>
      <template v-for="(item, i) in items">
        <v-subheader v-if="item.header" :key="`subheader-${i}`" v-text="item.header" />
        <v-divider v-else-if="item.divider" :key="`divider-${i}`" />
        <list-group v-else-if="item.group" :key="`group-${i}`" :item="item" />
        <list-item v-else :key="`item-${i}`" :icon="item.icon" :subtext="item.subtext" :text="item.text"
          :to="item.to" />
      </template>
    </v-list>
  </v-navigation-drawer>
</template>
<script>
import ListGroup from "@/components/List/Group";
import ListItem from "@/components/List/Item";

export default {
  name: "AppDrawer",
  components: {
    ListGroup,
    ListItem
  },
  props: {
    expanded: {
      type: Boolean,
      default: true
    },
    drawWidth: {
      type: [Number, String],
      default: "260"
    },
    showDrawer: Boolean
  },
  data() {
    return {
      mini: false,
      items: [],
      drawer: true,
      scrollSettings: {
        maxScrollbarLength: 160
      }
    };
  },

  computed: {
    computeGroupActive() {
      return true;
    }
  },
  watch: {
    showDrawer: {
      handler(val) {
        this.drawer = val;
      },
      immediate: true
    }
  },
  created() {
    this.items = [
      { header: "Apps" },
      {
        text: "codacon",
        icon: "transform",
        group: "/codacon",
        children: [
          {
            to: `/codacon/oldsource-management`,
            text: "Old Source Management",
          },
          {
            to: `/codacon/newsource-management`,
            text: "New Source Management",
          },
        ],
      },
      {
        text: "codadevs",
        icon: "mdi-code-tags",
        group: "/codadevs",
        children: [
          {
            to: `/codadevs/api-management`,
            text: "API Management",
          },
          {
            to: `/codadevs/batch-management`,
            text: "Batch Management",
          },
          {
            to: `/codadevs/studio`,
            text: "codadevs Studio",
          },
          {
            to: `/codadevs/classflow`,
            text: "Class Flow",
          },
          {
            to: `/codadevs/flow`,
            text: "CodaDevs Flow",
          },
          {
            to: `/codadevs/sql-management`,
            text: "SQL Management",
          },
          {
            to: `/codadevs/common-management`,
            text: "Common Management",
          },
          {
            to: `/codadevs/message-management`,
            text: "Message Management",
          },
          {
            to: `/codadevs/template-management`,
            text: "Tempalte Management",
          },
          { to: `/codadevs/settings`, text: "Settings" },
        ],
      },
      {
        text: "codatest",
        icon: "engineering",
        group: "/codatest",
        children: [
          {
            to: `/codatest/codatest-studio`,
            text: "codaTest Studio",
          },
          {
            to: `/codatest/testcase-management`,
            text: "Testcase Management",
          },
          {
            to: `/codatest/setting`,
            text: "Setting",
          },
        ],
      },
      {
        text: "codadb",
        icon: "mdi-database",
        group: "/codadb",
        children: [
          {
            to: `/codadb/ddlmanagement`,
            text: "DDL Management",
          },
          {
            to: `/codadb/datasetmanagement`,
            text: "Dataset Management",
          },
          {
            to: `/codadb/datatestmanagement`,
            text: "DataTest Management",
          },
        ],
      },
      {
        text: "codashare",
        icon: "hub",
        group: "/codashare",
        children: [
          {
            to: `/codashare/course`,
            text: "Course",
          },
          {
            to: `/codashare/codaquiz`,
            text: "CodaQuiz",
          },
          {
            to: `/codashare/codablog`,
            text: "CodaBlog",
          },
        ],
      },
      {
        text: "tools",
        icon: "construction",
        group: "/tools",
        children: [
          {
            to: `/tools/review-source-code`,
            text: "Tools Review Source",
          },
          {
            to: `/tools/sync-jira`,
            text: "Tools Sync Jira",
          },
          {
            to: `/tools/count-source-code`,
            text: "Tools Count Source",
          },
          {
            to: `/tools/translate-comment`,
            text: "Tools Translate Comment",
          },
          {
            to: `/tools/translate-dict`,
            text: "Translate Dictionary",
          },
        ],
      },
    ];
  },

  methods: {
    genChildTarget(item, subItem) {
      if (subItem.href) return;
      if (subItem.component) {
        return {
          name: subItem.component
        };
      }
      return { name: `${item.group}/${subItem.name}` };
    }
  }
};
</script>

<style lang="sass" scoped>
.app--drawer
  overflow: hidden !important

  .drawer-menu--scroll
    height: calc(100vh - 48px)
    overflow: auto
</style>
