<template>
  <div id="template-management" class="acoda-container">
    <v-card>
      <v-system-bar lights-out></v-system-bar>
      <v-carousel
        :continuous="false"
        :cycle="false"
        :show-arrows="true"
        hide-delimiter-background
        delimiter-icon="mdi-minus"
      >
        <v-carousel-item v-for="(slide, i) in slides" :key="i">
          <v-sheet :color="colors[i]" height="100%" tile>
            <v-row class="fill-height" align="center" justify="center">
              <div v-html="slide.html"></div>
            </v-row>
          </v-sheet>
        </v-carousel-item>
      </v-carousel>
      <v-list two-line>
        <v-list-item>
          <v-list-item-avatar>
            <v-img src="https://cdn.vuetifyjs.com/images/john.png"></v-img>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>John Leider</v-list-item-title>
            <v-list-item-subtitle>Author</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      colors: [
        "green",
        "secondary",
        "yellow darken-4",
        "red lighten-2",
        "orange darken-1",
      ],
      slides: [
        {
          html: "<h1># What’s an EBS Volume?</h1><ul><li>An EBS (Elastic Block Store) Volume is a network drive you can attach to your instances while they run</li><li>It allows your instances to persist data, even after their termination</li><li>They can only be mounted to one instance at a time (at the CCP level)</li><li>They are bound to a specific availability zone</li><li>Analogy:Think of them as a “network USB stick”</li><li>Free tier: 30 GB of free EBS storage of type General Purpose (SSD) orMagnetic per month</li></ul>",
        },
        {
          html: "<h1>EBS Volume</h1><ul><li>It’s a network drive (i.e. not a physical drive)<ul><li>It uses the network to communicate the instance, which means there might be a bit of latency</li><li>It can be detached from an EC2 instance and attached to another one quickly</li></ul></li><li>It’s locked to an Availability Zone (AZ)<ul><li>An EBS Volume in us-east-1a cannot be attached to us-east-1b</li><li>To move a volume across, you first need to snapshot it</li></ul></li><li>Have a provisioned capacity (size in GBs, and IOPS)<ul><li>You get billed for all the provisioned capacity</li><li>You can increase the capacity of the drive over time</li></ul></li></ul>",
        },
        {
          html: "<h1>EBS – Delete on Termination attribute</h1><ul><li>Controls the EBS behaviour when an EC2 instance terminates<ul><li>By default, the root EBS volume is deleted (attribute enabled)</li><li>By default, any other attached EBS volume is not deleted (attribute disabled)</li></ul></li><li>This can be controlled by the AWS console / AWS CLI</li><li>Use case: preserve root volume when instance is terminated</li></ul>",
        },
        {
          html: "<h1>EBS Snapshots</h1><ul><li>Make a backup (snapshot) of your EBS volume at a point in time</li><li>Not necessary to detach volume to do snapshot, but recommended</li><li>Can copy snapshots across AZ or Region</li></ul>",
        },
        {
          html: "<h1>AMI Overview</h1><ul><li>AMI = Amazon Machine Image</li><li>AMI are a customization of an EC2 instance<ul><li>You add your own software, configuration, operating system, monitoring...</li><li>Faster boot / configuration time because all your software is pre-packaged</li></ul></li><li>AMI are built for a specific region (and can be copied across regions)</li><li>You can launch EC2 instances from:<ul><li>A Public AMI: AWS provided</li><li>Your own AMI: you make and maintain them yourself</li><li>An AWS Marketplace AMI: an AMI someone else made (and potentially sells)</li></ul></li></ul>",
        },
        {
          html: "<h1>AMI Process (from an EC2 instance)</h1><ul><li>Start an EC2 instance and customize it</li><li>Stop the instance (for data integrity)</li><li>Build an AMI – this will also create EBS snapshots</li><li>Launch instances from other AMIs</li></ul>",
        },
        {
          html: "<h1>EC2 Instance Store</h1><ul><li>EBS volumes are network drives with good but “limited” performance</li><li>If you need a high-performance hardware disk, use EC2 Instance Store</li><li>Better I/O performance</li><li>EC2 Instance Store lose their storage if they’re stopped (ephemeral)</li><li>Good for buffer / cache / scratch data / temporary content</li><li>Risk of data loss if hardware fails</li><li>Backups and Replication are your responsibility</li></ul>",
        },
        {
          html: "<h1>EBS Volume Types</h1><ul><li>EBS Volumes come in 6 types<ul><li>gp2 / gp3 (SSD): General purpose SSD volume that balances price and performance for a wide variety of workloads</li><li>io1 / io2 (SSD): Highest-performance SSD volume for mission-critical low-latency or high-throughput workloads</li><li>st1 (HDD): Low cost HDD volume designed for frequently accessed, throughput-intensive workloads</li></ul></li><li>sc1 (HDD): Lowest cost HDD volume designed for less frequently accessed workloads</li><li>EBS Volumes are characterized in Size | Throughput | IOPS (I/O Ops Per Sec)</li><li>When in doubt always consult the AWS documentation – it’s good!</li><li>Only gp2/gp3 and io1/io2 can be used as boot volumes</li></ul>",
        },
        {
          html: "<h1>EBS Volume Types Use cases</h1><p>General Purpose SSD</p><ul><li>Cost effective storage, low-latency</li><li>System boot volumes,Virtual desktops, Development and test environments</li><li>1 GiB - 16 TiB</li><li>gp3:<ul><li>Baseline of 3,000 IOPS and throughput of 125 MiB/s</li><li>Can increase IOPS up to 16,000 and throughput up to 1000 MiB/s independently</li></ul></li><li>gp2:<ul><li>Small gp2 volumes can burst IOPS to 3,000</li><li>Size of the volume and IOPS are linked, max IOPS is 16,000</li><li>3 IOPS per GB, means at 5,334 GB we are at the max IOPS</li></ul></li></ul>",
        },
        {
          html: "<h1>EBS Volume Types Use cases</h1><p>Provisioned IOPS (PIOPS) SSD</p><ul><li>Critical business applications with sustained IOPS performance</li><li>Or applications that need more than 16,000 IOPS</li><li>Great for databases workloads (sensitive to storage perf and consistency)</li><li>io1/io2 (4 GiB - 16 TiB):<ul><li>Max PIOPS: 64,000 for Nitro EC2 instances &amp; 32,000 for other</li><li>Can increase PIOPS independently from storage size</li><li>io2 have more durability and more IOPS per GiB (at the same price as io1)</li></ul></li><li>io2 Block Express (4 GiB – 64 TiB):<ul><li>Sub-millisecond latency</li><li>Max PIOPS: 256,000 with an IOPS:GiB ratio of 1,000:1</li></ul></li><li>Supports EBS Multi-attach</li></ul>",
        },
        {
          html: "<h1>EBS Volume Types Use cases</h1><p>Hard Disk Drives (HDD)</p><ul><li>Cannot be a boot volume</li><li>125 MiB to 16 TiB</li><li>Throughput Optimized HDD (st1)<ul><li>Big Data, Data Warehouses, Log Processing</li><li>Max throughput 500 MiB/s – max IOPS 500</li></ul></li><li>Cold HDD (sc1):<ul><li>For data that is infrequently accessed</li><li>Scenarios where lowest cost is important</li><li>Max throughput 250 MiB/s – max IOPS 250</li></ul></li></ul>",
        },
        {
          html: "<h1>EBS Multi-Attach – io1/io2 family</h1><ul><li>Attach the same EBS volume to multiple EC2 instances in the same AZ</li><li>Each instance has full read &amp; write permissions to the volume</li><li>Use case:<ul><li>Achieve higher application availability in clustered Linux applications (ex:Teradata)</li><li>Applications must manage concurrent write operations</li></ul></li><li>Must use a file system that’s cluster-aware (not XFS, EX4, etc...)</li></ul>",
        },
        {
          html: "<h1>EFS – Elastic File System</h1><ul><li>Managed NFS (network file system) that can be mounted on many EC2</li><li>EFS works with EC2 instances in multi-AZ</li><li>Highly available, scalable, expensive (3x gp2), pay per use</li><li>Use cases: content management, web serving, data sharing, Wordpress</li><li>Uses NFSv4.1 protocol</li><li>Uses security group to control access to EFS</li><li>Compatible with Linux based AMI (not Windows)</li><li>Encryption at rest using KMS</li><li>POSIX file system (~Linux) that has a standard file API</li><li>File system scales automatically, pay-per-use, no capacity planning!</li></ul>",
        },
        {
          html: "<h1>EFS – Performance &amp; Storage Classes</h1><ul><li>EFS Scale<ul><li>1000s of concurrent NFS clients, 10 GB+ /s throughput</li><li>Grow to Petabyte-scale network file system, automatically</li></ul></li><li>Performance mode (set at EFS creation time)<ul><li>General purpose (default): latency-sensitive use cases (web server, CMS, etc...)</li><li>Max I/O – higher latency, throughput, highly parallel (big data, media processing)</li></ul></li><li>Throughput mode<ul><li>Bursting (1 TB = 50MiB/s + burst of up to 100MiB/s)</li><li>Provisioned: set your throughput regardless of storage size, ex: 1 GiB/s for 1 TB storage</li></ul></li><li>Storage Tiers (lifecycle management feature – move file after N days)<ul><li>Standard: for frequently accessed files</li><li>Infrequent access (EFS-IA): cost to retrieve files, lower price to store</li></ul></li></ul>",
        },
        {
          html: "<h1>EBS vs EFS – Elastic Block Storage</h1><ul><li>EBS volumes...<ul><li>can be attached to only one instance at a time</li><li>are locked at the Availability Zone (AZ) level</li><li>gp2: IO increases if the disk size increases</li><li>io1: can increase IO independently</li></ul></li><li>To migrate an EBS volume across AZ<ul><li>Take a snapshot</li><li>Restore the snapshot to another AZ</li><li>EBS backups use IO and you shouldn’t run them while your application is handling a lot of traffic</li></ul></li><li>Root EBS Volumes of instances get terminated by default if the EC2 instance gets terminated. (you can disable that)</li><li>Mounting 100s of instances across AZ</li><li>EFS share website files (WordPress)</li><li>Only for Linux Instances (POSIX)</li><li>EFS has a higher price point than EBS</li><li>Can leverage EFS-IA for cost savings</li><li>Remember: EFS vs EBS vs Instance Store</li></ul>",
        },
      ],
    };
  },

  methods: {},
};
</script>