<template>
  <div>
    <mavon-editor v-model="content"/>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: "# your markdown content",
      options: {
        linkAttributes: {
          attrs: {
            target: "_blank",
            rel: "noopener"
          }
        },
        katex: {
          throwOnError: false,
          errorColor: "#cc0000"
        },
        icons: "font-awesome",
        githubToc: {
          tocFirstLevel: 2,
          tocLastLevel: 3,
          tocClassName: "toc",
          anchorLinkSymbol: "",
          anchorLinkSpace: false,
          anchorClassName: "anchor",
          anchorLinkSymbolClassName: "octicon octicon-link"
        }
      }
    };
  }
};
</script>