import Vue from 'vue'
import VueRouter from 'vue-router'
import { DefaultLayout } from '@/components/layouts'
import Login from '@/views/Login.vue'
import Register from '@/views/Register.vue'
import NewProject from '@/views/NewProject.vue'
import ListSourceCobol from '@/views/cobol2java/ListSourceCobol.vue'
import FunctionDashboard from '@/views/cobol2java/FunctionDashboard.vue'
import MappingSource from '@/views/cobol2java/MappingSource.vue'
import ListFunctions from '@/views/cobol2java/ListFunctions.vue'
import ProjectSetting from '@/views/akacon/ProjectSetting.vue'
import ConfirmComponent from '@/views/akatest/ConfirmComponent.vue'
import EditBlog from '@/views/codablog/EditBlog.vue'
// import { auth } from "../firebaseConfig.js";

// start codashare
import CodaQuiz from '@/views/codashare/CodaQuiz.vue'
import CodaCourse from '@/views/codashare/course/CodaCourse.vue'
import CourseDetail from '@/views/codashare/course/Detail.vue'
import CourseLesson from '@/views/codashare/course/Lesson.vue'
import CourseBook from '@/views/codashare/course/Book.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: DefaultLayout,
        meta: {
            title: 'Home',
            group: 'apps',
            icon: '',
        },
        redirect: '/dashboard',
        children: [
            {
                path: '/dashboard',
                name: 'Dashboard',
                meta: {
                    title: 'Dashboard',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: () => import(/* webpackChunkName: "dashboard" */ '@/views/ListProject.vue'),
            },
            {
                path: '/newproject',
                name: 'New Project',
                meta: {
                    title: 'New Project',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: NewProject,
            },
            {
                path: '/:projectId/cobolsource',
                name: 'Cobol Source',
                meta: {
                    title: 'Source Cobol',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: ListSourceCobol,
            },
            {
                path: '/:projectId/setting',
                name: 'Project Setting',
                meta: {
                    title: 'Project Setting',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: ProjectSetting,
            },
            {
                path: '/:projectId/function/list',
                name: 'List Function',
                meta: {
                    title: 'List Function',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: ListFunctions,
            },
            {
                path: '/:projectId/:functionId/dashboard',
                name: 'Function Dashboard',
                meta: {
                    title: 'Function Dashboard',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: FunctionDashboard,
            },
            {
                path: '/:projectId/:functionId/mapping',
                name: 'Mapping Source',
                meta: {
                    title: 'Mapping Source',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: MappingSource,
            },
            {
                path: '/akatest/confirm-component',
                name: 'Mapping Source',
                meta: {
                    title: 'Mapping Source',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: false
                },
                component: ConfirmComponent,
            },
            {
                path: '/blog/edit',
                name: 'Mapping Source',
                meta: {
                    title: 'Mapping Source',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: false
                },
                component: EditBlog,
            },

            ////////////////////////////////////////////////////
            // START CODASHARE
            ////////////////////////////////////////////////////
            {
                path: '/codashare/codaquiz',
                name: 'CodaQuiz',
                meta: {
                    title: 'CodaQuiz',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: CodaQuiz,
            },
            {
                path: '/codashare/course',
                name: 'Course',
                meta: {
                    title: 'Course',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: CodaCourse,
            },
            {
                path: '/codashare/course-detail',
                name: 'Course Detail',
                meta: {
                    title: 'Course Detail',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: CourseDetail,
            },
            {
                path: '/codashare/course-lesson',
                name: 'Course Lesson',
                meta: {
                    title: 'Course Lesson',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: CourseLesson,
            },
            {
                path: '/codashare/course-book',
                name: 'Course Book',
                meta: {
                    title: 'Course Book',
                    group: 'apps',
                    icon: 'dashboard',
                    requiresAuth: true
                },
                component: CourseBook,
            },
        ],
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/register',
        name: 'Register',
        component: Register
    },
]

const router = new VueRouter({
    mode: 'history',
    routes
})

router.beforeEach((to, from, next) => {
    // const requiresAuth = to.matched.some(x => x.meta.requiresAuth)
    // const currentUser = auth.currentUser

    // if(requiresAuth) {
    //     if(currentUser) {
    //         if(currentUser.emailVerified) {
    //             next()
    //         } else {
    //             next('/login')
    //         }
    //     } else {
    //         next('/login')
    //     }
    // } else {
        next()
    // }
})

export default router
