<template>
  <v-container>
    <v-row justify="center">
      <v-card width="100%">
        <v-app-bar text dense color="transparent" elevation="0">
          <v-toolbar-title>
            <h4>Function List</h4>
          </v-toolbar-title>
          <v-spacer />
          <v-btn color="primary" @click="goToSourceCobol()">Source cobol</v-btn>
        </v-app-bar>
        <v-divider></v-divider>
        <v-card-text class="pa-0">
          <v-data-table
            :headers="headers"
            :items="functionList"
            item-key="name"
            show-select
            class="elevation-1"
            @click:row="handRowClick"
          >
            <template v-slot:item.name="{ item }">
              <v-chip :color="getColor(item.status)" dark>{{ item.name }}</v-chip>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import { storage } from "../../firebaseConfig.js";
import axios from "axios";

export default {
  name: "ListFunction",
  data() {
    return {
      selected: [],
      headers: [
        {
          text: "File name",
          align: "left",
          sortable: true,
          value: "name"
        },
        { text: "Last Modified", value: "lastModified" },
        { text: "File size", value: "size" },
        { text: "Action", value: "action", sortable: false }
      ],
      functionList: []
    };
  },
  created() {
    this.$store.commit("setMenu", [
      { header: "Apps" },
      {
        text: "akacon",
        icon: "dashboard",
        to: "/dashboard",
        group: `/${this.$route.params.projectId}`,
        children: [
          { to: `/${this.$route.params.projectId}/function/list`, text: "Function List" },
          { to: `/${this.$route.params.projectId}/cobolsource`, text: "Source A" },
          { to: `/${this.$route.params.projectId}/setting`, text: "Setting" }
        ]
      }
    ]);
    this.getListFunction();
  },
  computed: {},
  methods: {
    getColor(status) {
      if (status == 1) return "red";
      else return "green";
    },
    async getListFunction() {
      this.functionList = [];
      const token = await this.$store.state.currentUser.getIdToken();
      axios
        .post(
          "https://us-central1-akacon-f132f.cloudfunctions.net/cobol2java/function/list",
          {
            projectId: this.$route.params.projectId
          },
          {
            headers: { Authorization: "Bearer " + token }
          }
        )
        .then(res => {
          this.functionList = res.data;
        })
        .catch(err => {
          console.log(err.response);
          this.error = err.errors;
          this.loading = false;
        });
    },
    deleteItem(item) {
      storage
        .ref(item.fullPath)
        .delete()
        .then(function() {
          this.getCobolSource();
        })
        .catch(function(error) {
          console.log(error.response);
        });
    },
    handRowClick(item) {
      console.log(item);
      this.$router.push(
        `/${this.$route.params.projectId}/${item.name}/dashboard`
      );
    },
    goToSourceCobol() {
      this.$router.push(`/${this.$route.params.projectId}/cobolsource`);
    }
  }
};
</script>

