<template>
  <div id="template-management" class="acoda-container">
    <v-layout row wrap v-for="(item, idx) in items" :key="idx" my-3>
      <v-flex
        @click="
          openLesson(item);
          return;
        "
        v-bind="{ [`xs${item.flex}`]: true, [`offset-xs${item.offset}`]: true }"
        v-html="item.template"
      >
      </v-flex>
    </v-layout>

    <v-dialog v-model="dialog" max-width="750px">
      <v-card>
        <Tweet :tweetData="tweet" />
        <div style="margin: 16px">
          <v-textarea
            counter
            label="Translate"
            :value="tweet.content"
          ></v-textarea>
        </div>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> Close </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import Tweet from "@/components/Tweet";
import { marked } from "marked";
import axios from "axios";

export default {
  data() {
    return {
      items: [],
      tweet: {
        author: {
          _id: 123,
          username: "demo",
          name: "demo",
        },
        createdAt: "2022-07-02",
        content: "demo content",
        replies: [],
        retweets: [],
        quoteTweets: [],
        likes: [],
      },
      dialog: false,
      data: "",
    };
  },
  components: {
    Tweet,
  },

  methods: {
    openLesson(item) {
      this.dialog = !this.dialog;
      this.tweet.content = item.template;
    },
  },

  created() {
    axios
      .get(
        "https://raw.githubusercontent.com/nguyenthedan/readwithdan/main/" + this.$route.params.path
      )
      .then((resp) => {
        console.log(resp.data);
        let data = resp.data;
        let statement = data.split(/\r?\n/);
        statement.forEach((item) => {
          let contentArr = item.split(" codademitran: ");
          let markedText = marked(contentArr[0]);
          console.log(markedText);
          if (contentArr.length == 2) {
            // let tranHtml = `<div class="v-alert v-sheet theme--light elevation-2 v-alert--border v-alert--border-left"><div class="v-alert__wrapper"><div class="v-alert__content"> ` +
            //   contentArr[1] + ` </div><div class="v-alert__border v-alert__border--left light-blue accent-4 v-alert__border--has-color"></div></div></div>`;
            let tranHtml =
              `<div style="border-left: 6px solid #ccc !important; background-color: #ddffff !important;color: #000 !important;padding: 0.01em 16px;"> ` +
              contentArr[1] +
              ` </div>`;
            markedText = markedText + tranHtml;
          }
          this.items.push({
            template: markedText,
            flex: 10,
            offset: 1,
          });
        });
      });
  },
};
</script>